.flex {
    display: flex;
}

.center {
    justify-content: center;
}

.between {
    justify-content: space-between;
}

.grid {
    display: grid;
}

.columns {
    grid-auto-flow: column;
}

.rows {
    grid-auto-flow: row;
}

.round {
    border-radius: var(--themeBorderRadius);
}

.border {
    border: 1px solid;
}

.small {
    font-size: 0.65em;
}

.medium {
    font-size: 0.85em;
}

.large {
    font-size: 1em;
}

.xLarge {
    font-size: 1.3em;
}



.fit {
    width: fit-content;
}

.fill {
    width: fill-available;
    width: -webkit-fill-available;
}

.full {
    width: 100vw;
}

.fitH {
    height: fit-content;
}

.pointer {
    cursor: pointer;
}

.alignCenter {
    align-items: center;
    align-content: center;
}

a,
a:hover,
a:visited,
a:active {
    color: inherit;
    list-style: none;
    text-decoration: none;
}

button {
    padding: 0.5em 0.6em;
    background-color: rgba(180, 180, 180, 0.4);
    border-radius: 0.2em;
    border: none;
}

button:hover {
    opacity: 0.8;
}


.rotate {
    animation: rotate 1s forwards;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}










