div#header {
    padding: 0.5em 0em 0.2em 0em;
    z-index: 50;
    position: sticky;
    box-shadow: var(--boxShadowHeader);
    border-bottom: 1px solid;
    border-radius: 0px 0px 1em 1em;
    background-image: linear-gradient(-45deg, #9c78db5c, transparent);
}

#menuLogo {
    width: 1.7em;
    height: 1.5em;
}

ul#menuCategory {
    list-style-type: none;
    font-size: 0.9em;
}

ul#menuCategory li {
    padding: 0.3em 1em;
    cursor: pointer;
    font-weight: 500;
}

ul#menuCategory li:hover {
    opacity: 0.8;
    color: var(--logoBlue);
    /* background-color: rgba(152, 152, 152, 0.243); */
}